  border-color: rgba(0, 0, 0, .275);
  border-radius: 5px;
  margin-bottom: 24px;
  padding: 0 16px;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-weight: 500;
  display: none;
}

.cat-info {
  max-width: 1000px;
  gap: 24px;
